<template>
  <v-container>
    <v-row justify="center">
      <v-sheet class="reservation" width="400">
        <v-form class="form" @submit.prevent="handleReserve">
          <v-container class="d-flex flex-column">
            <!-- 予約日 -->
            <v-dialog v-model="showDateSlotDialog" max-width="400px">
              <template v-slot:activator="{ props }">
                <v-text-field v-model="reserveDate" label="予約希望日を選択してください" prepend-icon="mdi-calendar"
                  readonly v-bind="props"></v-text-field>
              </template>
              <v-card>
                <v-card-title class="text-h6">予約希望日選択</v-card-title>
                <v-card-text>
                  <v-list>
                    <v-list-item v-for="(slot, index) in daySlots" :key="index" @click="selectDaySlot(slot)">
                      <v-list-item-title>{{ slot }}</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-card-text>
                <v-card-actions>
                  <v-btn text @click="showDateSlotDialog = false">Cancel</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!-- 時刻 -->
            <div v-if="this.validateDate(this.reserveDate)">
              <v-dialog v-model="showTimeSlotDialog" max-width="400px">
                <template v-slot:activator="{ props }">
                  <v-text-field v-model="reserveTime" label="希望時刻を選択してください" prepend-icon="mdi-clock-time-four-outline"
                    readonly v-bind="props"></v-text-field>
                </template>
                <v-card>
                  <v-card-title class="text-h6">予約時刻選択</v-card-title>
                  <v-card-text>
                    <v-list>
                      <v-list-item v-for="(slot, index) in timeSlots" :key="index" @click="selectTimeSlot(slot)">
                        <v-list-item-title>{{ slot }}</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn text @click="showTimeSlotDialog = false">Cancel</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </div>

            <!-- 備考欄 -->
            <div v-if="this.validateDate(this.reserveDate) && this.validateTime(this.reserveTime)">
              <v-textarea prepend-icon="mdi-comment-edit-outline"
                label="備考があれば入力してください"
                density="compact"
                v-model.trim="this.note"
                maxlength="100"
                rows="2"
              ></v-textarea>
            </div>
          </v-container>
          <v-btn class="submit-button" @click="this.handleReserve" append-icon="mdi-calendar"
            v-if="this.validateDate(this.reserveDate) && this.validateTime(this.reserveTime)">予約確認画面へ</v-btn>
        </v-form>
      </v-sheet>
    </v-row>
    <v-row>
      <v-col>
        <v-btn prepend-icon="mdi-arrow-left-circle-outline" @click="$emit('switch-view', 'clientInfo')">ユーザページTOPへ</v-btn>
      </v-col>
    </v-row>

    <!-- 予約内容確認ダイアログ -->
    <v-dialog v-model="showConfirmDialog" max-width="500">
      <v-card>
        <v-card-title>この内容で予約します</v-card-title>
        <v-card-text>
          <v-row>
            <v-table fixed-header density="compact">
              <tbody>
                <tr>
                  <td class="text-right">予約日 :</td>
                  <td class="text-left">{{ reserveDate }}</td>
                </tr>
                <tr>
                  <td class="text-right">時刻 :</td>
                  <td class="text-left">{{ reserveTime }}</td>
                </tr>
                <tr>
                  <td class="text-right">備考 :</td>
                  <td class="text-left">{{ note }}</td>
                </tr>
              </tbody>
            </v-table>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showConfirmDialog = false">修正する</v-btn>
          <v-btn
            class="register-button"
            text="予約する"
            append-icon="mdi-book-plus-outline"
            @click="registerReservation"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 予約完了ダイアログ -->
    <v-dialog v-model="showCompleteDialog" max-width="400">
      <v-card prepend-icon="mdi-check-circle-outline" title="予約完了">
        <v-card-text>
          予約が完了しました。<br>
          ※ 予約キャンセルは、電話にて承ります。
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="$emit('switch-view', 'clientInfo')">閉じる</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { generateTimeSlots } from '@/utils';

export default {
  name: 'Reservation',
  props: {
    currentView: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      // 日付選択関連
      showDateSlotDialog: false,
      reserveDate: null,
      daySlots: [],
      dateError: null,
      // 時間選択関連
      showTimeSlotDialog: false,
      reserveTime: null,
      timeSlots: [],
      timeError: null,
      // 確認画面
      showConfirmDialog: false,
      note: '',
      // 完了画面
      showCompleteDialog: false,
    }
  },
  created() {
    // 予約可能日リストを生成
    this.generateDaySlots();
    // 時間帯リストを生成
    this.timeSlots = generateTimeSlots("10:00", "18:00", 15);
  },
  methods: {
    generateDaySlots() {
      // TODO : Backendから予約可能日を取得し、リスト化
      const daysOfWeek = ["日", "月", "火", "水", "木", "金", "土"];
      const today = new Date();

      for (let i = 0; i <= 2; i++) {
        const targetDate = new Date(today);
        targetDate.setDate(today.getDate() + i);

        const year = targetDate.getFullYear();
        const month = String(targetDate.getMonth() + 1).padStart(2, "0");
        const day = String(targetDate.getDate()).padStart(2, "0");
        const dayOfWeek = daysOfWeek[targetDate.getDay()];

        this.daySlots.push(`${year}-${month}-${day} (${dayOfWeek})`);
      }
    },
    // 予約日選択処理
    selectDaySlot(slot) {
      this.reserveDate = slot;
      this.reserveTime = null;
      this.showDateSlotDialog = false;
    },
    // 予約時間処理
    selectTimeSlot(slot) {
      this.reserveTime = slot;
      this.showTimeSlotDialog = false;
    },
    validateDate(date) {
      return date && date !== null;
    },
    validateTime(time) {
      return time && time !== '';
    },
    /** 予約ボタン押下時処理 */
    handleReserve() {
      // バリデーションチェック
      this.dateError = null;
      this.timeError = null;

      if (!this.validateDate(this.reserveDate)) {
        this.dateError = "Please enter a valid date.";
      }
      if (!this.validateTime(this.reserveTime)) {
        this.timeError = "Please enter a valid time.";
      }

      if (!this.dateError && !this.timeError) {
        this.showConfirmDialog = true;
      }
    },
    /** 予約登録処理 */
    registerReservation() {
      // TODO : Backendに予約登録リクエストを送信
      this.showConfirmDialog = false;
      this.showCompleteDialog = true;
    },
  }
};
</script>

<style scoped>
.reservation {
  display: flex;
  justify-content: center;
  align-items: center;
}
.submit-button {
  color: white;
  background-color: #339966;
